.support {
  }

.foundations {
display: block;
padding-top: 24px;
border: 1px solid #4F2EE8;
border-radius: 16px;
background-color: #4F2EE8;
height: 352px;
}



.blocks {
    margin-top: 40px;
    height: 188px;
    overflow: hidden;
}

.block {
    display: block; 
}

.block:not(:last-child) {
    margin-bottom: 20px;
}

.foundations-title {
    /* position: fixed; */
    z-index: 1;
    display: flex;
    align-items: center;
    gap: 12px;
    color: #FFFFFF;
}

.foundations-title-text {
font-size: 22px;
}

.gotofoundation {
    display: flex;
    gap: 16px;
    color: #FFFFFF;
}
.image {
    filter: brightness(0) invert(1)
}
.support-content {
    padding-left: 40px;

}
.buttons {
    display: flex;
    gap: 5px;
    justify-content: center;
}

.button-scroll-up,
.button-scroll-down {
    display: flex;
    z-index: 1;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    border-color: #FFFFFF;
    margin-top: 16px;
    /* margin-left: 111px; */
    
}

/* .button-scroll-up,
.button-scroll-down :hover {
    background-color:cadetblue ;
} */


@media screen and (min-width: 768px) {
    .container.support {
        max-width: 335px;
        max-height: 474;
        padding-left: 32px;
        padding-right: 32px;
        margin-left: 0;
    }
}

@media screen and (min-width: 1440px) {
    .container.support {
        max-width: 356px;
        max-height: 474;
        padding-left: 24px;
        padding-right: 24px;
    }
}

